/* eslint-disable @typescript-eslint/no-unused-vars */
// components/layouts/ConditionalLayout.tsx
'use client';

import React, { useState } from 'react';
import { usePathname, useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import { Calendar, LogOut, User, Settings, Menu, X, Globe } from 'lucide-react';
import Link from 'next/link';
import OrganizerClientLayout from '@/app/organizer/OrganizerClientLayout';

interface ConditionalLayoutProps {
    children: React.ReactNode;
}

const ConditionalLayout: React.FC<ConditionalLayoutProps> = ({ children }) => {
    const pathname = usePathname();
    const router = useRouter();
    const { user, logout } = useAuth();
    const [showUserMenu, setShowUserMenu] = useState(false);

    // Check if we're in the organizer section
    const isOrganizerRoute = pathname?.startsWith('/organizer');

    // Check if we're on the dashboard page specifically
    const isDashboardPage = pathname === '/organizer' || pathname === '/organizer/dashboard';

    // Check if we're on auth pages (login/register)
    const isAuthPage = pathname?.includes('/login') || pathname?.includes('/register');

    const handleSignOut = async () => {
        try {
            await logout();
            setShowUserMenu(false);
            router.push('/login');
        } catch (error) {
            console.error('Sign out error:', error);
        }
    };

    if (isOrganizerRoute) {
        // For organizer dashboard, render without header
        if (isDashboardPage) {
            return (
                <div className="min-h-screen">
                    <OrganizerClientLayout>
                        {children}
                    </OrganizerClientLayout>
                </div>
            );
        }

        // For other organizer routes, use the organizer layout with header functionality
        return (
            <div className="min-h-screen">
                {/* Header for Non-Dashboard Organizer Pages */}
                <header className="bg-white/80 backdrop-blur-xl shadow-sm sticky top-0 z-50 border-b border-white/20">
                    <div className="max-w-full mx-auto px-6 lg:px-8">
                        <div className="flex justify-between items-center py-4">
                            {/* Logo */}
                            <Link href="/" className="flex items-center hover:opacity-80 transition-opacity">
                                <Calendar className="h-8 w-8 text-blue-600 mr-3" />
                                <h1 className="text-2xl font-bold text-gray-900">EventHub</h1>
                                <span className="ml-3 px-2 py-1 bg-blue-100 text-blue-800 text-xs font-medium rounded-full">
                                    Organizer
                                </span>
                            </Link>

                            {/* User Section */}
                            <div className="flex items-center space-x-6">
                                {user ? (
                                    <>
                                        {/* Welcome Message */}
                                        <div className="text-sm text-gray-600 hidden sm:block">
                                            Welcome back, {user.firstName || 'Organizer'}
                                        </div>

                                        {/* User Menu */}
                                        <div className="relative">
                                            <button
                                                onClick={() => setShowUserMenu(!showUserMenu)}
                                                className="flex items-center space-x-2 text-gray-700 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 rounded-lg p-2 transition-colors"
                                            >
                                                <div className="w-8 h-8 bg-blue-600 rounded-full flex items-center justify-center">
                                                    <User className="h-4 w-4 text-white" />
                                                </div>
                                                <span className="hidden sm:block font-medium">
                                                    {user.firstName} {user.lastName}
                                                </span>
                                            </button>

                                            {/* Dropdown Menu */}
                                            {showUserMenu && (
                                                <div className="absolute right-0 mt-2 w-56 bg-white rounded-xl shadow-lg border border-gray-200 py-2 z-50">
                                                    <div className="px-4 py-3 border-b border-gray-100">
                                                        <p className="text-sm font-medium text-gray-900">
                                                            {user.firstName} {user.lastName}
                                                        </p>
                                                        <p className="text-sm text-gray-500 truncate">
                                                            {user.email}
                                                        </p>
                                                        <p className="text-xs text-blue-600 font-medium mt-1">
                                                            Organizer Account
                                                        </p>
                                                    </div>

                                                    <div className="py-2">
                                                        <Link
                                                            href="/organizer/profile"
                                                            className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                                                            onClick={() => setShowUserMenu(false)}
                                                        >
                                                            <User className="h-4 w-4 mr-3" />
                                                            My Profile
                                                        </Link>

                                                        <Link
                                                            href="/organizer/settings"
                                                            className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                                                            onClick={() => setShowUserMenu(false)}
                                                        >
                                                            <Settings className="h-4 w-4 mr-3" />
                                                            Account Settings
                                                        </Link>

                                                        <Link
                                                            href="/organizer"
                                                            className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                                                            onClick={() => setShowUserMenu(false)}
                                                        >
                                                            <Calendar className="h-4 w-4 mr-3" />
                                                            Dashboard
                                                        </Link>

                                                        <div className="border-t border-gray-100 my-2"></div>

                                                        <Link
                                                            href="/"
                                                            className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                                                            onClick={() => setShowUserMenu(false)}
                                                        >
                                                            <Globe className="h-4 w-4 mr-3" />
                                                            View Public Site
                                                        </Link>

                                                        <div className="border-t border-gray-100 my-2"></div>

                                                        <button
                                                            onClick={handleSignOut}
                                                            className="flex items-center w-full px-4 py-2 text-sm text-red-700 hover:bg-red-50 transition-colors"
                                                        >
                                                            <LogOut className="h-4 w-4 mr-3" />
                                                            Sign Out
                                                        </button>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    </>
                                ) : (
                                    /* Guest User Options */
                                    <div className="flex items-center space-x-4">
                                        <Link
                                            href="/login"
                                            className="text-gray-600 hover:text-gray-900 transition-colors"
                                        >
                                            Sign In
                                        </Link>
                                        <Link
                                            href="/register"
                                            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition-colors"
                                        >
                                            Sign Up
                                        </Link>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                </header>

                {/* Click outside to close menu */}
                {showUserMenu && (
                    <div
                        className="fixed inset-0 z-40"
                        onClick={() => setShowUserMenu(false)}
                    />
                )}

                {/* Organizer Layout Content */}
                <OrganizerClientLayout>
                    {children}
                </OrganizerClientLayout>
            </div>
        );
    }

    if (isAuthPage) {
        // For auth pages, render without any layout
        return <>{children}</>;
    }

    // For regular pages (like events homepage), render without header
    return <>{children}</>;
};

export default ConditionalLayout;